import React, { useState } from "react";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Me from "./components/Me";
import Casa from "./components/Casa";
import Side from "./components/Side";
import Menu from "./components/Menu";
import { Switch } from "react-router-dom";
import { Route } from "react-router-dom/cjs/react-router-dom";
import { Consumer } from "./Context";
import Modal from "./components/portfolio/Modal";

function App() {
  const [scroll, setScroll] = useState(2);
  const [display, setDisplay] = useState("none");
  return (
    <Consumer>
      {({ displayModal }) => {
        return (
          <div className="container">
            <div className="row">
              <Switch>
                <Route
                  exact
                  path="/"
                  render={(routeprops) => {
                    return (
                      <>
                        <Casa />
                      </>
                    );
                  }}
                />
                <Route
                  path="/eu"
                  render={(routeprops) => {
                    return (
                      <>
                        <div className="col-12 principal pr-2 pr-md-0">
                          <div className="row">
                            <div className="col-12 col-md-4 col-lg-3 no-p ">
                              <Side />
                            </div>
                            <div className="col-12 col-md-8 col-lg-9 no-p">
                              <Menu
                                scroll={scroll}
                                display={display}
                                setDisplay={setDisplay}
                              />
                              <Me
                                setScroll={setScroll}
                                routeprops={routeprops}
                                display={display}
                                setDisplay={setDisplay}
                              />
                            </div>
                          </div>
                        </div>
                      </>
                    );
                  }}
                />
              </Switch>
              {displayModal === "none" ? "" : <Modal />}
            </div>
          </div>
        );
      }}
    </Consumer>
  );
}

export default App;
