import React from "react";
import styled from "styled-components";
import Sobre from "./Sobre";
import Portfolio from "./portfolio/Portfolio";
import Contactos from "./Contactos";

function Me(props) {
  const { setScroll, routeprops, display } = props;

  const handleScroll = (event) => {
    let afterCasa = document.getElementById("afterCasa");
    let sobre = document.getElementById("sobre").scrollHeight;
    let portfolio = document.getElementById("portfolio").scrollHeight + sobre;
    /**/
    const scrollY = afterCasa.scrollTop;
    
    if (scrollY >= 0 && scrollY < sobre) {
      setScroll(2);
    } else if (scrollY >= sobre && scrollY < portfolio) {
      setScroll(3);
    } else if (scrollY >= portfolio) {
      setScroll(4);
    } /**/
  };

  window.onload = () => {
    let afterCasa = document.getElementById("afterCasa");
    const { hash } = routeprops.location;
    if (hash === "#sobre") {
      afterCasa.scrollTop = 0;
      setScroll(2);
    } else if (hash === "#portfolio") {
      afterCasa.scrollTop = 644;
      setScroll(3);
    } else if (hash === "#contactos") {
      setScroll(4);
      afterCasa.scrollTop = 1554;
    }
  };

  return (
    <AfterCasa
      className="col-12 p-0"
      id="afterCasa"
      onScroll={handleScroll}
      display={display}
    >
      <Sobre />
      <Portfolio />
      <Contactos />
    </AfterCasa>
  );
}
const AfterCasa = styled.div`
  background-color: var(--mainBlackOpc);
  height: calc(100vh - 66px);
  @media screen and (max-width: 768px) {
    height: calc(
      100vh - ${({ display }) => (display !== "none" ? "245px" : "66px")}
    ) !important;
  }
  overflow-y: scroll;
  color: var(--mainWhite);
  p {
    color: var(--mainGray);
  }
  &::-webkit-scrollbar {
    max-width: 0.4rem !important;
  }

  &::-webkit-scrollbar-track {
    background: var(--mainGreen) !important;
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--mainBlack) !important;
  }
  /*firefox*/
  scrollbar-width: thin;
  scrollbar-color: var(--mainBlack) var(--mainGreen);
`;
export default Me;
