import React, { Component } from "react";
import { Title } from "./Title";
import styled from "styled-components";
import { MdMail, MdPhone } from "react-icons/md";
import Button from "./Button";
import emailjs from "emailjs-com";

class Contactos extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      assunto: "",
      email: "",
      feedback: "",
    };
  }

  inputHandle = (event) => {
    const inputName = event.target.name;
    const value = event.target.value;
    this.setState({ [inputName]: value });
  };

  submit = (event) => {
    event.preventDefault();
    const {
      REACT_APP_EMAILJS_RECEIVER: service_id,
      REACT_APP_EMAILJS_TEMPLATEID: template_id,
      REACT_APP_EMAILJS_USERID: user_id,
    } = process.env;
    const { name, assunto, email, feedback } = this.state;
    this.sendFeedback({
      message_html: feedback,
      from_name: name,
      subject: assunto,
      reply_to: email,
    });
  };
  sendFeedback = (variables) => {
    emailjs
      .send(
        "gmail",
        "template_nYoPb8KT",
        variables,
        "user_DpSBPprT2v9LK1EZQMobb"
      )
      .then(
        (result) => {
          //console.log(result.text);
          alert("Email enviado com sucesso.");
        },
        (error) => {
          alert("Email não enviado. Tente denovo");
          /*console.log(
          "falhou o envio************************************************"
        );
        console.log(error.text);
        console.log("************************************************");*/
        }
      );
  };
  render() {
    return (
      <Container className="col-12 p-5" id="contactos">
        <Title w={"82px"}>Contactos</Title>
        <div className="col-12 pt-5">
          <ul className="col-12 text-center">
            <li>
              <span>
                <MdMail />
              </span>
              dalciomacuetegarcia@gmail.com
            </li>
            <li>
              <span>
                <MdPhone />
              </span>
              +244 931 396 454
            </li>
          </ul>
          <h4>Contacte-me</h4>
          <form onSubmit={this.submit} className="form col-12 pb-4">
            <div className="row">
              <div className="col-12 col-lg-6">
                <label htmlFor="name">
                  Nome <span>*</span>
                </label>
                <input
                  type="text"
                  name="name"
                  className="form-control"
                  id="name"
                  required
                  onChange={this.inputHandle}
                />
                <label htmlFor="assunto">Assunto</label>
                <input
                  type="text"
                  name="assunto"
                  id="assunto"
                  className="form-control"
                  onChange={this.inputHandle}
                />
                <label htmlFor="email">
                  Email <span>*</span>
                </label>
                <input
                  type="email"
                  name="email"
                  id="email"
                  required
                  className="form-control"
                />
              </div>
              <div className="col-12 col-lg-6">
                <textarea
                  name="feedback"
                  id="feedback"
                  className="form-control"
                  placeholder="Conteúdo"
                  cols="30"
                  required
                />
              </div>
            </div>
            <br />
            <Button width="100%">Enviar</Button>
          </form>
        </div>
      </Container>
    );
  }
}
const Container = styled.div`
  background-color: var(--mainBlack);
  min-height: calc(100vh - 66px);
  form {
    background-color: var(--mainGreenOpc) !important;
  }
  li {
    span {
      color: var(--mainGreen);
      margin-right: 16px;
    }
    :last-child {
      margin-top: 16px;
    }
  }
  label {
    margin: 16px 0;
  }
  textarea {
    margin-top: 56px;
    height: 226px;
  }
  @media screen and (max-width: 768px) {
    li {
      span {
        display: block;
      }
    }
  }
  @media screen and (max-width: 992px) {
    textarea {
      margin-top: 16px !important;
    }
  }
`;

export default Contactos;
