import React, { Component } from "react";
import { Title } from "./Title";
import ProgressBar from "./ProgressBar";
import styled from "styled-components";
import { MdDevices, MdTimer } from "react-icons/md";
import { Box } from "./Box";

class Sobre extends Component {
  render() {
    return (
      <Sob className="col-12 p-5" id="sobre">
        <Title w={"80px"}>Sobre Me</Title>
        <div className="col-12 pt-5">
          <p className="col-12 p-0 pb-3 p">
            Eu Sou um desenvolvedor Front-End. Um louco apaixonado por UI/UE
          </p>
          <div className="container p-0">
            <div className="row">
              <div className="col-12 col-lg-4 col-xl-3">
                <div className="row p-3 pt-lg-2">
                  <Box
                    className="col-12 col-md-6 col-lg-12 text-center box first"
                    child="first"
                  >
                    <span>
                      <MdDevices />
                    </span>
                    <div>Responsividade</div>
                    <p>Crio layouts que rodem em qualquer dispositivo</p>
                  </Box>
                  <Box
                    className="col-12 col-md-6 col-lg-12 text-center box"
                    child="last"
                  >
                    <span>
                      <MdTimer />
                    </span>
                    <div>Velocidade</div>
                    <p>
                      A velocidade ajuda a melhorar a experiência do usuário.
                    </p>
                  </Box>
                </div>
              </div>
              <div className="col-12 col-lg-8 col-xl-9 mt-3 m-md-0">
                <h4 className="pb-3">Minhas Habilidades</h4>
                <ProgressBar title={"HTML5, CSS3"} w={8} />
                <ProgressBar title={"Javascript"} w={6} />
                <ProgressBar title={"Node js"} w={5} />
                <ProgressBar title={"React js"} w={7} />
                <ProgressBar title={"UI Design"} w={5} />
                <ProgressBar title={"Photoshop"} w={4} />
              </div>
            </div>
          </div>
        </div>
      </Sob>
    );
  }
}
const Sob = styled.div`
  .p {
    text-align: center;
  }
  background-color: var(--mainBlack);
  .box1 {
    padding: 0 5px 10px 5px !important;
    span {
      font-size: 24px;
      color: var(--mainGreen);
    }
    :first-child {
      background-color: var(--mainWhiteOpc);
      color: var(--mainBlack);
      p {
        color: var(--mainBlackOpc);
      }
    }
    :last-child {
      border: 1px dotted var(--mainWhite);
    }
  }
  /*
  @media screen and (max-width: ){
    
  }*/
`;
export default Sobre;
