import React from "react";
import styled from "styled-components";
import Button from "./Button";
import Particles from "react-particles-js";
import { MdArrowDownward } from "react-icons/md";
import { Link } from "react-router-dom";
import Typewriter from "typewriter-effect";
///import { useState } from "react";

function Casa(props) {
  /*const txt = "Hello World!";
  const [texto, setTexto] = useState("");
  const [i, setI] = useState(0);
  const speed = 5;*/

  /*const baixar = () => {
    alert("baixou o arquivo");
  };*/

  /*const typeWriter = () => {
    if (i < txt.length) {
      setTexto(texto + txt.charAt(i));
      setI(i + 1);
      setTimeout(typeWriter, speed);
    }
  };*/

  const handleScroll = (event) => {
    console.log(event.target.offsetParent);
  };

  window.addEventListener("scroll", handleScroll);
  return (
    <Home className="col-12" id="casa" onScroll={handleScroll}>
      <div className="header">
        <div id="wrapper">
          <span>{"<>"}</span>
          <span>
            <Typewriter
              options={{
                strings: ["Hello World", "Olá Mundo"],
                autoStart: true,
                loop: true,
              }}
            />
          </span>
          <span>{"</>"}</span>
        </div>
        <h2 id="name">Dálcio Garcia</h2>
        <hr />
        <h6>Desenvolvedor web e Web Designer</h6>
        <br />
        <Button
          width={"150px"}
          onClick={() => {
            /*baixar()*/
          }}
        >
          Baixar o CV
        </Button>
        <div className="view">
          <Link to="/eu">
            <span>
              <MdArrowDownward />
            </span>
          </Link>
        </div>
      </div>
      <div className="particle">
        <Particles
          height="100vh"
          width="100vw"
          params={{
            particles: {
              number: {
                value: 70,
              },
              size: {
                value: 3,
              },
            },
            interactivity: {
              events: {
                onhover: {
                  enable: true,
                  mode: "repulse",
                },
              },
            },
          }}
        />
      </div>
    </Home>
  );
}
const Home = styled.div`
  min-height: 100vh;
  width: 100vw;
  padding-top: calc(50vh - 125px);
  color: var(--mainWhite);
  background-color: var(--mainBlack);
  text-align: center;
  overflow: hidden;
  #name {
    text-transform: uppercase;
  }
  #wrapper {
    span {
      display: inline-block;
    }
  }
  .header {
    max-width: 288px;
    position: absolute;
    z-index: 1;
    hr {
      border-bottom: 1px solid var(--mainGreen);
    }
    @media screen and (min-width: 336px) {
      margin-left: calc(50vw - 130.975px);
    }
    margin-bottom: 10px;
  }
  .particle {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
  }
  .view {
    span {
      font-wight: bold;
      font-size: 25px;
      background-color: var(--mainGreenOpc3);
      padding: 5px 10px 8px 10px;
      border-radius: 50%;
      cursor: pointer;
      :hover {
        transition: all 0.4s ease;
        a {
          color: var(--mainGreen) !important;
        }
        background-color: var(--mainGreenOpc2);
      }
    }
    -webkit-animation: next 0.4s linear infinite;
    animation: next 0.4s linear infinite;
  }

  @keyframes next {
    100% {
      margin-top: 20px;
    }
    50% {
      margin-top: 15px;
    }
    0% {
      margin-top: 10px;
    }
  }
  @-webkit-keyframes next {
    100% {
      margin-top: 20px;
    }
    50% {
      margin-top: 15px;
    }
    0% {
      margin-top: 10px;
    }
  }
`;
export default Casa;
