import React from "react";
import styled from "styled-components";
import { Box } from "./Box";
import { MdLaptopMac, MdFormatPaint } from "react-icons/md";
import Button from "./Button";
import Foto from "./img/dalcio-260w.jpg";

function Side(props) {
  return (
    <Lado>
      <div className="col-12 pt-4">
        <div className="imagem">
          <img src={Foto} alt="Dálcio Garcia" />
        </div>
      </div>
      <h2 className="col-12 pt-4 pb-4 mt-4 mb-4 m-md-0">Dálcio Garcia</h2>
      <div className="col-12">
        <Box className="col-12 " child="first">
          <span>
            <MdLaptopMac />
          </span>
          <div> Desenvolvedor Front-end </div>
        </Box>
        <Box className="col-12">
          <span>
            <MdFormatPaint />
          </span>
          <div> Web Designer </div>
        </Box>
      </div>
      <p className="col-12 p-4  mt-4 mb-4 m-md-0 text-align-center">
        Eu Sou um desenvolvedor front-end. Um louco apaixonado por Experiencia
        do usuário e Interação , crio layouts responsivos e dinâmicos.
      </p>
      <div className="col-12">
        <Button width="100%"> Baixar o CV </Button>
      </div>
    </Lado>
  );
}
const Lado = styled.div`
  padding-bottom: 20px;
  text-align: center;
  overflow-y: scroll;
  h2 {
    color: var(--mainGreen);
  }
  p {
    color: var(--mainGray);
  }
  img {
    height: 200px;
    width: 200px;
    border-radius: 100%;
  }
  @media screen and (max-width: 767px) {
    overflow-y: hidden !important;
    padding: 16px 64px !important;
    .imagem img {
      margin-top: 16px;
    }
    .col-12 {
      padding: 0 !important;
    }
  }
  background-color: var(--mainBlack);
  @media screen and (min-width: 768px) {
    height: calc(100vh - 6px);
  }
  @media screen and (min-height: 600px) {
    overflow-y: hidden !important;
  }
  &::-webkit-scrollbar {
    max-width: 0.4rem !important;
  }

  &::-webkit-scrollbar-track {
    background: var(--mainGreen) !important;
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--mainBlack) !important;
  }
  /*firefox*/
  scrollbar-width: thin;
  scrollbar-color: var(--mainBlack) var(--mainGreen);
`;
export default Side;
