import React from "react";
import styled from "styled-components";
import { MdTranslate, MdMenu, MdClear } from "react-icons/md";
import { useState } from "react";
import { HashLink } from "react-router-hash-link";

function Menu(props) {
  const { scroll, display, setDisplay } = props;
  const [active, setActive] = useState(scroll);
  const changeActive = (nth) => {
    setActive(nth);
  };
  try {
    let afterCasa = document.getElementById("afterCasa");
    afterCasa.addEventListener("scroll", () => {
      changeActive(scroll);
    });
  } catch (exception) {}

  return (
    <Header active={active} display={display}>
      <div
        className="toggle"
        onClick={() => setDisplay(display === "none" ? "block" : "none")}
      >
        {display === "none" ? (
          <MdMenu />
        ) : (
          <span id="mdclear">
            <MdClear />
          </span>
        )}
      </div>
      <ul>
        <li
          onClick={() => {
            changeActive(1);
            setDisplay("none");
          }}
          id="li-1"
        >
          <HashLink to="/"> Casa </HashLink>
        </li>
        <li
          onClick={() => {
            changeActive(2);
            setDisplay("none");
          }}
          id="li-2"
        >
          <HashLink to="/eu#sobre"> Sobre Me </HashLink>
        </li>
        <li
          onClick={() => {
            changeActive(3);
            setDisplay("none");
          }}
          id="li-3"
        >
          <HashLink to="/eu#portfolio"> Portfólio </HashLink>
        </li>
        <li
          onClick={() => {
            changeActive(4);
            setDisplay("none");
          }}
          id="li-4"
        >
          <HashLink to="/eu#contactos"> Contactos </HashLink>
        </li>
        <li id="translate">
          <MdTranslate />
        </li>
      </ul>
    </Header>
  );
}
const Header = styled.div`
  background-color: var(--mainBlack);
  border-bottom: 3px solid var(--mainGreen);
  color: var(--mainWhite);
  li {
    display: inline-block;
    padding: 16px 16px 0 16px;
    :hover {
      transition: all 0.5s ease-out;
      a {
        color: var(--mainGreen) !important;
      }
      color: var(--mainGreen) !important;
      cursor: pointer;
    }
  }
  #translate {
    float: right;
  }
  li:nth-child(${({ active }) => active}) {
    a {
      color: var(--mainGreen) !important;
    }
  }
  .toggle {
    display: none;
  }
  @media screen and (max-width: 768px) {
    min-height: 59px;
    .toggle {
      font-size: 30px;
      display: block;
      z-index: 1;
      float: right;
      margin-right: 16px;
      font-weight: strong;
      cursor: pointer;
      :hover {
        color: var(--mainGreen) !important;
      }
    }
    ul {
      display: ${({ display }) => display}; /**/
      /*height: 200px;*/
      padding: 59px 0 0 0;
      text-align: center;
      li {
        display: block;
      }
      #translate {
        position: absolute;
        z-index: 1;
        top: -2px;
        right: 60px;
      }
    }
  }
`;
export default Menu;
