import React from "react";
import { Title } from "../Title";
import { useState } from "react";
import styled from "styled-components";
import Projectos from "./Projectos";
import { Consumer } from "../../Context";
import Button from "../Button";

function Portfolio(props) {
  const [ulLi, setUlLi] = useState(1);
  /*const current = (nth) => {
    setUlLi(nth);
  };*/
  return (
    <Consumer>
      {({ projectos }) => {
        return (
          <Container className="col-12 p-5" li={ulLi} id="portfolio">
            <Title w={"82px"}>Projectos</Title>
            <div className="col-12 pt-5">
              <ul className="col-12 mb-0">
                <li onClick={() => setUlLi(1)}>Todos</li>
                {/*
                <li onClick={() => setUlLi(2)}>Mobile</li>
                <li onClick={() => setUlLi(3)}>Web</li>
                <li onClick={() => setUlLi(4)}>Desktop</li>*/}
              </ul>
              <br />
              <div className="col-12">
                <div className="row">
                  {projectos.map((projecto) => {
                    return <Projectos {...projecto} key={projecto.id} />;
                  })}
                </div>
              </div>
              <br />
              <a href="https://github.com/Dalcio" target="BLANK">
                <Button width="100%">Ver Mais no Gihub</Button>
              </a>
            </div>
          </Container>
        );
      }}
    </Consumer>
  );
}

const Container = styled.div`
  ul {
    text-align: center;
    li {
      cursor: pointer;
      display: inline-block;
      padding: 5px 20px;
      :hover {
        color: var(--mainGreen);
      }
      :nth-child(${({ li }) => li}) {
        transition: all 0.5s ease;
        background-color: var(--mainGreen);
        color: var(--mainBlack);
        :hover {
          color: var(--mainBlack);
        }
      }
    }
  }
`;
export default Portfolio;
